"use client";

import React, { useEffect, useState } from "react";

type Applicant = {
  _id: string;
  status: string;
  createdAt: string;
};

export default function DashboardPage() {
  const [applicants, setApplicants] = useState<Applicant[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchApplicants = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/applicants"); // your backend endpoint
        if (!res.ok) throw new Error("Failed to fetch applicants");
        const json = await res.json();

        // ✅ use the correct field from your backend: "items"
        const items = json.items || [];

        // ✅ map safely
        const formatted = items.map((item: any) => ({
          _id: item._id,
          status: item.status || "Pending",
          createdAt: item.createdAt,
        }));

        setApplicants(formatted);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchApplicants();
  }, []);

  if (loading) return <p>Loading dashboard...</p>;
  if (error) return <p className="text-red-500">Error: {error}</p>;

  // ✅ total from backend OR local count
  const totalApplicants = applicants.length || 0;

  // ✅ example of filtering by date/status
  const today = new Date().toISOString().split("T")[0];
  const todaySubmissions = applicants.filter((app) =>
    app.createdAt.startsWith(today)
  ).length;

  // ✅ filter by status if you later add "Approved", "Rejected", etc.
  const pending = applicants.filter((app) => app.status === "Pending").length;

  return (
    <div className="p-8">
      <h1 className="text-2xl font-bold mb-4">Dashboard</h1>

      <div className="grid grid-cols-3 gap-6">
        <div className="p-4 bg-white rounded-2xl shadow">
          <h2 className="text-lg font-semibold">Total Applications</h2>
          <p className="text-3xl mt-2">{totalApplicants}</p>
        </div>

        <div className="p-4 bg-white rounded-2xl shadow">
          <h2 className="text-lg font-semibold">New Submissions (Today)</h2>
          <p className="text-3xl mt-2">{todaySubmissions}</p>
        </div>

        <div className="p-4 bg-white rounded-2xl shadow">
          <h2 className="text-lg font-semibold">Pending</h2>
          <p className="text-3xl mt-2">{pending}</p>
        </div>
      </div>
    </div>
  );
}
